@import url(https://fonts.googleapis.com/css?family=Work+Sans:300,400,500,700&display=swap);
*{
	margin: 0;
	padding: 0;
}

body {
  background: url(/PixabayApp/static/media/imageedit_3_5890261322.5d68548b.png) no-repeat top / cover;
  background-attachment: fixed;
  font-family: 'Work Sans', sans-serif;
}
.imageList__container {
  border-radius: 1px;
  box-shadow: -1px 2px 34px -13px rgba(0,0,0,0.55);
  text-align: left;
  overflow: hidden;
}

.imageList__image {
  width: 100%;
  height: 200px;
}

.image__details {
  background: #34495e;
}

.image__details button {
  width: 100%;
  border: 0;
  background-color: #2c3e50;
  color: #fff;
  cursor: pointer
}

.image__details button:hover {
    background-color: #fff;
    color: #000;

}
.imageSearch {
  width: 85%;
  text-align: center;
  position: relative;
  margin: 13% auto 20px auto;
}

.imageSearch__form {
  display: flex;
  margin-left: 30px;
}

.imageSearch__form input {
  border: transparent;
  padding: 8px;
  width: 88%;
  font-size: 16px;
  border-radius: 10px;
  outline: none;

}

.imageSearch__form button {
  width: 80px;
  height: 40px;
  background: transparent;
  border: solid 2px #19232d;
  color: #8da1a1;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
  margin-left: 10px;
}


.imageSearch__form button:hover {
  color: #fff;
  border-color: #fff;
}

h1 {
  color: #8da1a1;
  margin-bottom: 20px;
}
.error {
	color: #fff;
	max-width: 200px;
	margin: 0 auto;
}
.img-container {
  max-width: 85%;
  display: flex;
  margin: 100px auto;
}

.img-info {
  margin-top: 30px;
  margin-left: 30px;
}

.img-info h4 {
  margin-bottom: 20px;
}

.home-btn {
  display: block;
  width: 80px;
  height: 40px;
  background: transparent;
  border: solid 2px #19232d;
  color: #8da1a1;
  border-radius: 10px;
  outline: none;
  text-align: center;
  padding-top: 5px;
}

.home-btn:hover {
  text-decoration: none;
  color: #fff;
  border-color: #fff;
}


.imageView__img {
  max-width: 500px;
}


@media(max-width: 1170px) {
  .img-container {
    flex-direction: column;
  }

  .img-info {
    margin: 0;
  }
}



@media(max-width: 500px)  {
  .imageView__img {
  max-width: 300px;
  }
}
